import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import AllStock from '../views/AllStock.vue'
import DataSyncManage from '../views/DataSyncManage.vue'
import StockDetail from '../views/StockDetail.vue'
Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'AllStock',
    component: AllStock
  },
  {
    path: '/dataSync',
    name: 'dataSync',
    component: DataSyncManage
  },
  {
    path: '/stockDetail/:stockCode',
    name: 'stockDetaill',
    component: StockDetail
  }
]

const router = new VueRouter({
  routes
})

export default router
