





























import CandlesticChart from '../components/CandlesticChart.vue'
import { Component, Prop, Vue } from 'vue-property-decorator'
@Component({
  components: {
    candlesticChart: CandlesticChart
  }
})
export default class StockDetail extends Vue {
    private stockInfo = {
      stockCode: ''
    }

    private financeReportList = []

    getFinanceReportList (): void {
      const stockCode = this.stockInfo.stockCode
      this.axios({
        url: '/stockDataSpiderController/getStockFinanceReportList',
        method: 'get',
        params: {
          stockCode: stockCode
        }
      }).then((data) => {
        this.financeReportList = data.data.data
        console.log(this.financeReportList)
      })
    }

    mounted (): void {
      this.stockInfo.stockCode = this.$route.params.stockCode
      this.getFinanceReportList()
    }
}
