









































import { Component, Prop, Vue } from 'vue-property-decorator'
import PaginationSetting from './PaginationSetting'
@Component
export default class Pagination extends Vue {
    @Prop() private setting!: PaginationSetting;
    private pageSize = 15;
    private pageNum = 1;
    private total = 0;
    private totalPage = 0;
    handleCurrentChange (pageNum: number): void{
      this.pageNum = pageNum
      this.queryData(false)
    }

    clickPage (command: any): void {
      if (command === 'next') {
        this.pageNum++
      }
      if (command === 'previous') {
        this.pageNum--
      }
      if (command === 'first') {
        this.pageNum = 1
      }
      if (command === 'last') {
        this.pageNum = this.totalPage
      }
      this.queryData(false)
    }

    requestSucc (data: any) {
      console.log(data)
      this.pageNum = data.pageNum
      this.pageSize = data.pageSize
      this.total = data.total
      this.totalPage = Math.ceil(this.total / this.pageSize)
      this.setting.rows = data.list
      this.setting.loading = false
    }

    queryData (reset: boolean): void {
      if (reset) {
        this.pageNum = 1
      }
      this.setting.loading = true
      this.setting.params.pageSize = this.pageSize
      this.setting.params.pageNum = this.pageNum
      this.setting.requestMethod(this.setting.params).then(this.requestSucc)
    }

    mounted (): void {
      this.queryData(true)
    }
}
