






import { Component, Prop, Watch, Vue } from 'vue-property-decorator'
import PaginationSetting from '../common/PaginationSetting'
import Pagination from '../common/Pagination.vue'
import echarts from 'echarts'
import DateUtil from '../common/DateUtil'
@Component
export default class CandlesticChart extends Vue {
  @Prop() private stockInfo: any;
  @Prop() private width!: string;
  @Prop() private height!: string;
  mounted (): void {
    const divElement = this.$refs.chartContainer as HTMLDivElement
    const canvas = this.$refs.candlestickChart as HTMLCanvasElement
    canvas.setAttribute('height', '' + divElement.offsetHeight)
    canvas.setAttribute('width', '' + divElement.offsetWidth)
    setTimeout(() => {
      if (this.stockInfo.stockCode) {
        this.drawCandlesticChart()
      }
    })
  }

  calculateMA (dayCount: number, data: Array<Array<number>>): Array<number> {
    const result: Array<number> = []
    for (let i = 0, len = data.length; i < len; i++) {
      if (i < dayCount) {
        result.push(data[i][1])
        continue
      }
      let sum = 0
      for (let j = 0; j < dayCount; j++) {
        sum += data[i - j][1]
      }
      result.push(sum / dayCount)
    }
    return result
  }

  @Watch('stockInfo')
  public drawCandlesticChart (): void {
    console.log(this.stockInfo)
    const stockCode = this.stockInfo.stockCode
    const nowDate = new Date()
    const startDate = new Date(nowDate.getTime() - 730 * 24 * 60 * 60 * 1000)
    this.axios({
      url: '/stockDataSpiderController/getStockHistory',
      method: 'post',
      params: {
        startDate: DateUtil.getStringDate(startDate),
        endDate: DateUtil.getStringDate(new Date()),
        stockCode: stockCode
      }
    }).then(data => {
      const stockHistoryList: Array<any> = data.data.data
      const tradeDateList = []
      const priceVectorList: Array<Array<number>> = []
      const upColor = '#00da3c'
      const downColor = '#ec0000'
      const volumeList = []
      for (let i = 0; i < stockHistoryList.length; i++) {
        const tradeDate = stockHistoryList[i].tradeDate
        tradeDateList.push(tradeDate)
        const priceVector = [
          stockHistoryList[i].priceStartToday,
          stockHistoryList[i].priceNow,
          stockHistoryList[i].priceMinToday,
          stockHistoryList[i].priceMaxToday
        ]
        volumeList.push(stockHistoryList[i].turnVolume)
        priceVectorList.push(priceVector)
      }
      const mychart = echarts.init(
        this.$refs.candlestickChart as HTMLCanvasElement
      ) as any
      const stockName = this.stockInfo.stockName
      const option = {
        title: {
          text: stockName,
          left: 0
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        legend: {
          data: ['日K', 'MA5', 'MA10', 'MA20', 'MA30']
        },
        brush: {
          xAxisIndex: 'all',
          brushLink: 'all',
          outOfBrush: {
            colorAlpha: 0.1
          }
        },
        grid: [{
          left: '10%',
          right: '8%',
          height: '50%'
        },
        {
          left: '10%',
          right: '8%',
          top: '63%',
          height: '16%'
        }],
        xAxis: [{
          type: 'category',
          data: tradeDateList,
          scale: true,
          boundaryGap: false,
          axisLine: { onZero: false },
          splitLine: { show: false },
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax'
        },
        {
          type: 'category',
          gridIndex: 1,
          data: tradeDateList,
          scale: true,
          boundaryGap: false,
          axisLine: { onZero: false },
          axisTick: { show: false },
          splitLine: { show: false },
          axisLabel: { show: false },
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax'
        }],
        yAxis: [{
          scale: true,
          type: 'value',
          splitArea: {
            show: true
          }
        },
        {
          scale: true,
          gridIndex: 1,
          splitNumber: 2,
          axisLabel: { show: false },
          axisLine: { show: false },
          axisTick: { show: false },
          splitLine: { show: false }
        }],
        dataZoom: [
          {
            type: 'inside',
            start: 50,
            end: 100,
            xAxisIndex: [0, 1]
          },
          {
            show: true,
            type: 'slider',
            y: '90%',
            xAxisIndex: [0, 1],
            start: 0,
            end: 100
          }
        ],
        series: [
          {
            name: '日K',
            type: 'candlestick',
            data: priceVectorList,
            itemStyle: {
              normal: {
                color: '#ec0000',
                color0: '#00da3c',
                borderColor: '#8A0000',
                borderColor0: '#008F28'
              }
            },
            markPoint: {
              label: {
                normal: {
                  formatter: function (param: any) {
                    return param != null ? Math.round(param.value) : ''
                  }
                }
              },
              data: [
                {
                  name: 'XX标点',
                  coord: ['2013/5/31', 2300],
                  value: 2300,
                  itemStyle: {
                    normal: { color: 'rgb(41,60,85)' }
                  }
                },
                {
                  name: 'highest value',
                  type: 'max',
                  valueDim: 'highest'
                },
                {
                  name: 'lowest value',
                  type: 'min',
                  valueDim: 'lowest'
                },
                {
                  name: 'average value on close',
                  type: 'average',
                  valueDim: 'close'
                }
              ],
              tooltip: {
                formatter: function (param: any) {
                  return param.name + '<br>' + (param.data.coord || '')
                }
              }
            },
            markLine: {
              symbol: ['none', 'none'],
              data: [
                [
                  {
                    name: 'from lowest to highest',
                    type: 'min',
                    valueDim: 'lowest',
                    symbol: 'circle',
                    symbolSize: 0,
                    label: {
                      normal: { show: false },
                      emphasis: { show: false }
                    }
                  },
                  {
                    type: 'max',
                    valueDim: 'highest',
                    symbol: 'circle',
                    symbolSize: 0,
                    label: {
                      normal: { show: false },
                      emphasis: { show: false }
                    }
                  }
                ],
                {
                  name: 'min line on close',
                  type: 'min',
                  valueDim: 'close'
                },
                {
                  name: 'max line on close',
                  type: 'max',
                  valueDim: 'close'
                }
              ]
            }
          },
          {
            name: 'MA5',
            type: 'line',
            data: this.calculateMA(5, priceVectorList),
            smooth: true,
            lineStyle: {
              normal: { opacity: 0.5 }
            }
          },
          {
            name: 'MA10',
            type: 'line',
            data: this.calculateMA(10, priceVectorList),
            smooth: true,
            lineStyle: {
              normal: { opacity: 0.5 }
            }
          },
          {
            name: 'MA20',
            type: 'line',
            data: this.calculateMA(20, priceVectorList),
            smooth: true,
            lineStyle: {
              normal: { opacity: 0.5 }
            }
          },
          {
            name: 'MA30',
            type: 'line',
            data: this.calculateMA(30, priceVectorList),
            smooth: true,
            lineStyle: {
              normal: { opacity: 0.5 }
            }
          },
          {
            name: 'Volume',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: volumeList
          }
        ]
      }
      mychart.setOption(option)
    })
  }
}
