export default class DateUtil {
  public static getStringDate (date: Date): string {
    const year = date.getFullYear()
    const month = date.getMonth() + 1
    const day = date.getDate()
    const dateString = year + '-' + (month > 9 ? '' : '0') + month + '-' + (day > 9 ? '' : '0') + day
    return dateString
  }
}
