




































































































































import { Component, Prop, Vue } from 'vue-property-decorator'
import PaginationSetting from '../common/PaginationSetting'
import Pagination from '../common/Pagination.vue'
import echarts from 'echarts'
import CandlesticChart from '../components/CandlesticChart.vue'
import $ from 'jquery'
@Component({
  components: {
    pagination: Pagination,
    candlesticChart: CandlesticChart
  }
})
export default class AllStock extends Vue {
  private openedStockInfo: any = {
    stockCode: '',
    stockName: ''
  };

  private operationBarHeight = 0
  private filterWordList = [{ wordName: 'pe', wordDesc: '市盈率' }, { wordName: 'pb', wordDesc: '市净率' },
    { wordName: 'priceNow', wordDesc: '当前每股价格' }, { wordName: 'changePercent', wordDesc: '涨跌幅' },
    { wordName: 'roe', wordDesc: '净资产收益率' }, { wordName: 'roa', wordDesc: '总资产收益率' },
    { wordName: 'assetLiabilityRatio', wordDesc: '资产负债率' }]

  private candlestickChartDialogVisible = false;
  private paginationSetting: PaginationSetting = {
    rows: [],
    loading: false,
    requestMethod: (params: any) => {
      return this.axios({
        url: '/stockDataSpiderController/getPagedStockList',
        method: 'post',
        data: params
      }).then(function (data: any) {
        return data.data.data
      })
    },
    params: {
      searchKey: '',
      sortProp: '',
      sortOrder: '',
      peStart: 0,
      numberWordFilterList: [],
      boardCode: '',
      collected: ''
    }
  };

  resize (): void {
    setTimeout(() => {
      this.operationBarHeight = (this.$refs.operationBar as HTMLDivElement).offsetHeight
    })
  }

  addNumberWordFilter (): void {
    this.paginationSetting.params.numberWordFilterList.push({
      word: ''
    })
    this.resize()
  }

  removeNumberWordFilter (): void {
    this.paginationSetting.params.numberWordFilterList.splice(this.paginationSetting.params.numberWordFilterList.length - 1, 1)
    this.resize()
  }

  search (): void {
    (this.$refs.pagination as Pagination).queryData(true)
  }

  sortChange (column: any): void {
    this.paginationSetting.params.sortOrder = column.order
    this.paginationSetting.params.sortProp = column.prop
    this.search()
  }

  openStockDetail (stockInfo: any): void {
    this.$router.push('/stockDetail/' + stockInfo.stockCode)
  }

  openCandlestickChart (stockInfo: any): void {
    this.candlestickChartDialogVisible = true
    this.openedStockInfo = stockInfo
  }

  collectStock (stockInfo: any): void {
    this.axios({
      url: '/stockDataSpiderController/collectStock',
      method: 'get',
      params: {
        stockCode: stockInfo.stockCode
      }
    }).then((data) => {
      if (data.data.success) {
        this.$message({
          type: 'info',
          message: '收藏成功'
        })
        stockInfo.collected = true
      }
    })
  }

  uncollectStock (stockInfo: any): void {
    this.axios({
      url: '/stockDataSpiderController/uncollectStock',
      method: 'get',
      params: {
        stockCode: stockInfo.stockCode
      }
    }).then((data) => {
      if (data.data.success) {
        this.$message({
          type: 'info',
          message: '取消收藏成功'
        })
        stockInfo.collected = false
      }
    })
  }

  mounted (): void {
    this.operationBarHeight = (this.$refs.operationBar as HTMLDivElement).offsetHeight
  }
}
