






import { Component, Prop, Vue } from 'vue-property-decorator'
@Component
export default class DataSyncManage extends Vue {
  syncStockInfo (): void {
    this.axios
      .get('/stockDataSpiderController/startSyncDataTask')
      .then(data => {
        console.log(data)
        if (data.data.success) {
          this.$message({
            type: 'info',
            message: '开始同步数据'
          })
        }
      })
  }

  syncStockFinanceReport (): void {
    this.axios.get('/stockDataSpiderController/startSyncStockFinanceReportTask')
      .then(data => {
        console.log(data)
        if (data.data.success) {
          this.$message({
            type: 'info',
            message: '开始同步数据'
          })
        }
      })
  }
}
