








import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import $ from 'jquery'
@Component
export default class App extends Vue {
  private menuList: Array<any> = [{ path: '/', name: '股票列表' }, { path: '/dataSync', name: '信息同步' }]
  addMenu (path: string, name: string): void {
    this.menuList.push({
      path: path,
      name: name
    })
  }

  addStockDetailMenu (path: string): void {
    if (path.indexOf('stockDetail') > -1) {
      let i = 0
      for (i = 0; i < this.menuList.length; i++) {
        if (this.menuList[i].path.indexOf('stockDetail') > -1) {
          break
        }
      }
      this.menuList.splice(i, 1)
      this.addMenu(path, '股票详情')
    }
  }

  @Watch('$route')
  routeChange (to: any, from: any): void {
    this.addStockDetailMenu(to.path)
  }

  mounted (): void {
    this.addStockDetailMenu(this.$route.path)
  }
}
